"use client";

import React, { createContext, useContext, useState, useEffect } from "react";
import { ThemeProvider } from "next-themes";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { cn } from "@/lib/utils";

type Language = "en" | "es" | "fr" | "de" | "zh";

interface LanguageContextProps {
  language: Language;
  setLanguage: (lang: Language) => void;
}

const LanguageContext = createContext<LanguageContextProps | undefined>(undefined);

export const useLanguage = () => {
  const context = useContext(LanguageContext);
  if (!context) {
    throw new Error("useLanguage must be used within a LanguageProvider");
  }
  return context;
};

const navLinks = [
  { href: "/ai", label: "IA Personalizada" },
  { href: "/legacy", label: "Legado Digital" },
  { href: "/historical", label: "Personajes Hist√≥ricos" },
  { href: "/career", label: "Recomendaciones" },
  { href: "/settings", label: "Ajustes" },
];

export function Providers({ children }: { children: React.ReactNode }) {
  const pathname = usePathname();
  const [mounted, setMounted] = useState(false);
  const [language, setLanguage] = useState<Language>("es");

  useEffect(() => {
    setMounted(true);
    if (typeof window !== "undefined") {
      const browserLang = navigator.language.slice(0, 2);
      if (["en", "es", "fr", "de", "zh"].includes(browserLang)) {
        setLanguage(browserLang as Language);
      }
    }
  }, []);

  return (
    <ThemeProvider
      attribute="class"
      defaultTheme="system"
      enableSystem
      disableTransitionOnChange
    >
      <LanguageContext.Provider value={{ language, setLanguage }}>
        <header className="sticky top-0 z-50 w-full border-b border-gray-200 bg-white/75 backdrop-blur dark:border-gray-800 dark:bg-gray-950/75">
          <div className="container flex h-16 items-center justify-between">
            <Link href="/" className="flex items-center space-x-2">
              <span className="text-2xl font-bold">NEXO</span>
            </Link>
            <nav className="hidden md:flex items-center space-x-6">
              {navLinks.map(({ href, label }) => (
                <Link
                  key={href}
                  href={href}
                  className={cn(
                    "text-sm font-medium transition-colors hover:text-primary",
                    pathname === href
                      ? "text-foreground"
                      : "text-foreground/60"
                  )}
                >
                  {label}
                </Link>
              ))}
            </nav>
          </div>
        </header>
        <main className="flex-1">{children}</main>
      </LanguageContext.Provider>
    </ThemeProvider>
  );
}
